





















import Vue from 'vue';

export default Vue.extend({
  props: ['itemsLeft', 'clearCompleted', 'currentView'],
  computed: {
    itemSingularOrPlural(): string {
      return this.itemsLeft === 1 ? 'item' : 'items';
    },
    isActive(): boolean {
      return this.currentView === 'active';
    },
    isCompleted(): boolean {
      return this.currentView === 'completed';
    }
  }
});



